.App {
  text-align: center;
}

.App-logo {
  height: 150px;
  pointer-events: none;
  margin-bottom: 20px;
  margin-top: 20px;
}

.Products {
  width:50%;
  margin-top: 50px;
  margin-right: 30px;
  margin-left: 30px;
}
.List {
  width:50%;
  margin-top: 50px;
  margin-right: 30px;
  margin-left: 30px;
}

.Containers{
  display:flex;
  flex-direction:row;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #d63031;
  min-height: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

